.dark {
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  z-index: 9999;
}

.modal {
  position: absolute;
  background-color: antiquewhite;
  z-index: 99999;
  padding: 40px;
  text-align: center;
  height: 500px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 15px;
}

.list {
  text-align: left;
}

.space-between-col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80%;
}
